import React from 'react';
import styled from 'styled-components';


import BeltCard from './BeltCard';

import People from '../img/Belt/People.png'
import Medal from '../img/Belt/Medal.png'
import Headphone from '../img/Belt/Headphone.png'
import CardAnother from './CardAnother';

function BeltTwo(props) {
    return (
        <Styling>
            <div className="container">
                <CardAnother className="card"
                    secontTitle={"85.1%"}
                    text={"Mijozlar uzoq yillik"}
                />
                <CardAnother className="card"
                    secontTitle={"1886+"}
                    text={"Mijozga xizmat ko'rsatdik."}
                />
                <CardAnother className="card"
                    secontTitle={"2016"}
                    text={"Faoliyatimiz boshlangan yil"}
                />
                <CardAnother className="card"
                    secontTitle={"20+"}
                    text={"Marketing xizmat turlari"}
                />
            </div>
        </Styling>
    );
}

const Styling = styled.div`
    width: 100%;
    margin: 7rem auto;
    background: #F7F8FB;
    .container{
        display: flex;
        width: 90%;
        margin 0 auto;
        text-align: initial;
    }
    .container{
        display: flex;
        align-items: center;
        justify-content: center;
        text-align: center;
    }

    .card{
        padding: 50px;
    }
`

export default BeltTwo;