import React from 'react';
import styled from 'styled-components';


function ContactCard({href,img,title,subtitle}) {
    return (
        <div>
            <Styling href={href}>
                <img src={img} alt="" />
                <h1>{title}</h1>
                <p>{subtitle}</p>
            </Styling>
        </div>
    );
}

const Styling = styled.div`
    width: 295px;
    height: 200px;
    text-align: initial;
    border: 1px solid #D0D0D0;
    padding: 20px;
    margin: 0px;

    @media screen and (max-width: 1200px) {
        height:200px;
        width: 210px;
    
    }
`

export default ContactCard;