import React from 'react';
import styled from 'styled-components';


const Portfolio = ({img, feedback, avatar, title, subtitle }) => {
    return(
            <Person>
                <div className="feadback">
                    <div className="apostrov">
                        <img src={img} alt="apostrov" />
                        <img src={img} alt="apostrov" />
                    </div>
                    <h2>{feedback}</h2>
                </div>
                <div className="personal">
                    <img src={avatar} alt="person" />
                    <h1>{title}</h1>
                    <p>{subtitle}</p>
                </div>
            </Person>
    )
}

const Person = styled.div`
        width: 295px;
        margin-top: 5rem;
        .feadback{
            background: #FFFFFF;
            border: 1px solid #D0D0D0;
            box-sizing: border-box;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center; 
            width: 295px;
            height: 222px;
        }
        h2{
            margin: 20px 20px;
            font-family: Montserrat;
            font-style: normal;
            font-weight: 400;
            font-size: 16px;
            line-height: 22px;
            text-align: center;
        }

        .personal{
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center; 
            position:relative;
            bottom: 30px;
        }

        h1{
            font-family: Montserrat;
            font-style: normal;
            font-weight: 600;
            font-size: 16px;
            line-height: 22px;
        }
        p{
            font-family: 'Montserrat';
            font-style: normal;
            font-weight: 500;
            font-size: 14px;
            line-height: 17px;
            text-align: center;
        }
`

export default Portfolio;