import React from 'react';
import styled from 'styled-components';



function BeltCard({img, title, number}) {
    return (
        <div>
            <Card>
                {number && <h3>{number}</h3>}
                {img && <img src={img}/>}
                {title && <p>{title}</p>}
            </Card>
        </div>
    );
}

const Card = styled.div`
    display:flex;
    padding: 20px;
p{
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 22px;
}

img{
    width: 20px;
    height: 25px;
    margin-top: 23px;
    margin-right: 20px;
}

`

export default BeltCard;


