import React from 'react';
import SmmCard from './SmmCard';
import styled from "styled-components"

import one from "../img/seo/one.png"
import two from "../img/seo/two.png"
import three from "../img/seo/three.png"
import four from "../img/seo/four.png"

const SmmCardSection = () => {
    return ( 
        <Container>
            <SmmCard
            title={"Mijozlar kopayishi"}
            subtitle={"SEO-ni amalga oshirish orqali siz saytingiz oladigan malakali yetakchilar miqdorini oshirishingiz mumkin. Bu mijoz bo'lish imkoniyati yuqori bo'lgan qidiruvchilarni jalb qilishni anglatadi."}
            img={one}
            />
            <SmmCard
            title={"Geo-targeting"}
            subtitle={"Agar sizning biznesingiz jismoniy joylarga ega bo'lsa va sizning maqsadingiz iste'molchilarning ularga kelishini ta'minlash bo'lsa, geo-targeting juda foydali bo'lishi mumkin."}
            img={two}
            />
            <SmmCard
            title={"Brend qurilishi"}
            subtitle={"SEO sizga brend haqida xabardorlikni oshirishga yordam beradi, bu esa oxir-oqibat saytingiz trafigini va savdosini oshiradi."}
            img={three}
            />
            <SmmCard
            title={"Qidiruv sahifalarida yuqori orin"}
            subtitle={"Sizning veb-saytingiz qidiruv tizimining natijalari sahifalarida yuqori o'rinlarni egallab, har oyda shunday qilishda davom etsa, Internet foydalanuvchilari veb-saytingizga tashrifi kopayeveradi."}
            img={four}
            />
        </Container>
     );
}

const Container = styled.div`
width: 90%;
margin: 5% auto;
display: flex;
flex-wrap: wrap;
`
 
export default SmmCardSection;