import React from 'react';
import styled from 'styled-components';


import white from '../img/banner/white.png'
import blue from '../img/banner/blue.png'
import Title from './Title';



const Banner = ({title, subtitle, buttonBlue, buttonWhite, img, head,label, numberOne, numberTwo, numberThree,paragThree, paragTwo, paragOne}) => {
    return(
        <>
            <BannerStyled className="banner">
                <div className="title">
                    <Title
                        title={head}
                        subtitle={<p id='keyId'>{label}</p>}
                    />
                    {title && <h1 className='mainTitle'>{title}</h1>}
                    {subtitle && <p className='bannerParag'>{subtitle}</p>}
                    <div className="addition">
                        <div>
                            {numberOne && <h1>{numberOne}</h1>}
                            {paragOne && <p className='parag'>{paragOne}</p>}
                        </div>
                        <div>
                            {numberTwo && <h1>{numberTwo}</h1>}
                            {paragTwo && <p className='parag'>{paragTwo}</p>}
                        </div>
                        <div>
                            {numberThree && <h1>{numberThree}</h1>}
                            {paragThree && <p className='parag'>{paragThree}</p>}
                        </div>
                    </div>
                    <div className="buttons">
                        {buttonBlue && <button className=' text blue'>{buttonBlue}<img className='arrov' src={white} alt="=>" /> </button>}
                        {buttonWhite && <button className=' text white'>{buttonWhite}<img className='arrov' src={blue} alt="=>" /> </button>}
                    </div>
                </div>
                {img && <img className='bannerImage' src={img} alt="Banner main Image" />}
            </BannerStyled>

        </>
    )
}

const BannerStyled = styled.div`
    // height: 90vh;
    display: flex;
    justify-content: space-between;
    align-items:center;
    width: 90%;
    margin 0 auto;
    padding:50px 0% ;
    
    .mainTitle{
        align-self:center;
        width:90%;
        font-family: Montserrat;
        font-style: SemiBold;
        font-size: 48px;
        line-height: 59px;
        color: #030303;
        text-align: initial;
        margin:20px 0;
    }
    .bannerParag{
        font-family: Montserrat;
        font-style: Regular;
        font-size: 20px;
        line-height: 30px;
        line-height: 123%;
        text-align: initial;
    }
    .buttons{
        text-align: initial;
        margin-top: 20px;
    }
    .blue{
        background-color: #3779FF;
        color: #fff;
        width: 200px;
        height: 59px;
        border: 1px solid #000000;
        box-shadow: 3px 4px 2px black;
        margin-right: 2rem;
    }
    .white{
        background-color: #fff;
        color: #3779FF;
        width: 200px;
        height: 59px;
        border: 1px solid #3779FF;
    }

    .text{
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 22px;
    }

    .arrov{
        margin-left: 10px;
    }

    .arrovColor{
        color: #000;
    }

    // .bannerImage{
    //     width: 610px;
    //     height: 394px;
    // }

    .addition{
        display: flex;
        text-align: initial;
        justify-content: space-between;
    }

    .addition h1{
        font-family: Montserrat;
        font-style: normal;
        font-weight: 700;
        font-size: 20px;
        line-height: 24px;
    }

    .addition p{
        font-family: Montserrat;
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 22px;
    }
    .parag
    {
        width:130px;
    }
    .title
    {
        height:auto;
        display:flex;
        flex-direction:column;
        justify-content:space-between;
    }

    @media screen and (max-width: 1200px) {
    height: 130vh;
    display: flex;
    justify-content:space-around;
    align-items:center;
    flex-direction:column;
    flex-wrap:wrap;
    width: 90%;
    margin 0 5%;
    padding:0 0 ;
    
    .mainTitle{
        width:1000px;
        font-family: Montserrat;
        font-style: SemiBold;
        font-size: 48px;
        line-height: 59px;
        color: #030303;
        text-align: initial;
    }
    .bannerParag{
        font-family: Montserrat;
        font-style: Regular;
        font-size: 20px;
        line-height: 30px;
        line-height: 123%;
        text-align: initial;
    }
    .buttons{
        text-align: initial;
        margin-top: 20px;
    }
    .blue{
        background-color: #3779FF;
        color: #fff;
        width: 200px;
        height: 59px;
        border: 1px solid #000000;
        box-shadow: 3px 4px 2px black;
        margin-right: 2rem;
    }
    .white{
        background-color: #fff;
        color: #3779FF;
        width: 200px;
        height: 59px;
        border: 1px solid #3779FF;
    }

    .text{
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 22px;
    }
   

    .arrov{
        margin-left: 10px;
    }

    .arrovColor{
        color: #000;
    }

    .bannerImage{
        width: 990px;
    }

    .addition{
        width:1000px
        display: flex;
        text-align: initial;
    }

    .addition h1{
        font-family: Montserrat;
        font-style: normal;
        font-weight: 700;
        font-size: 20px;
        line-height: 24px;
    }

    .addition p{
        font-family: Montserrat;
        font-style: normal;
        font-weight: 500;
        font-size: 16px;
        line-height: 22px;
    }
      }

`

export default Banner;