import React from 'react';
import styled from "styled-components"

import ContactCard from "./ContactCard"

import location from '../img/contact/location.png'
import phone from '../img/contact/phone.png'
import mail from '../img/contact/mail.png'
import time from '../img/contact/time.png'

function Contact() {
    return (
        <Styling>
            <ContactCard  
                img={location}
                title={"Manzil"}
                subtitle={"Mirzo Ulug'bek ko'ch., 64-uy, Toshkent sh., O'zbekiston"}
            />
            <ContactCard 
                img={phone}
                title={"Telefon"}
                subtitle={"(71) 268 43-60"}
            />
            <ContactCard 
                img={mail}
                title={"E-mail"}
                subtitle={"info@oqila.uz"}
            />
            <ContactCard 
                img={time}
                title={"Aloqa vaqti"}
                subtitle={"Dushanba - Juma"}
            />
        </Styling>
    );
}

const Styling = styled.div`
    width:90%;
    display: flex;
    justify-content:space-between;
    width: 90%;
    margin: 20px auto;
    margin-bottom:60px;
`

export default Contact;