import React from 'react';
import styled from 'styled-components';


import brand1 from '../img/logos/brand1.png'
import brand2 from '../img/logos/brand2.png'
import brand3 from '../img/logos/brand3.png'
import brand4 from '../img/logos/brand4.png'
import brand5 from '../img/logos/brand5.png'
import brand6 from '../img/logos/brand6.png'
import brand7 from '../img/logos/brand7.png'

function LogoBelt(props) {
    return (
        <div>
            <Styling>
                <div className="container">
                    <img className='img1' src={brand1} alt="brand" />
                    <img className='img2' src={brand2} alt="brand" />
                    <img className='img3' src={brand3} alt="brand" />
                    <img className='img4' src={brand4} alt="brand" />
                    <img className='img5' src={brand5} alt="brand" />
                    <img className='img6' src={brand6} alt="brand" />
                    <img className='img7' src={brand7} alt="brand" />
                </div>
            </Styling>
        </div>
    );
}

const Styling = styled.div`
    background: #F7F8FB;
    margin-bottom: 5rem;
    .container{
        width: 90%;
        margin: 0 5%;
        position: relative;
        display:flex;
        justify-content:space-around;
        align-items:center;
    }

    img{
        width:auto;
        height:auto;
    }
`

export default LogoBelt;