import React from 'react';
import styled from "styled-components"

import Card from "./Card"

import bir from '../img/brending/bir.png'
import ikki from '../img/brending/ikki.png'
import uch from '../img/brending/uch.png'

function BrendingCards() {
    return (
        <Styling>
            <Card 
                href={"/"}
                img={bir}
                title={"Mahoratli dizaynerlar"}
                subtitle={"Sizning logotipingiz va reklamangiz mukammal ishlashligi uchun mahoratli va oz ishini eng zor dizayner va marketologlar jamoasi."}
            />
            <Card 
                href={"/"}
                img={ikki}
                title={"Togri strategiya"}
                subtitle={"Kashfiyot, tadqiqot, joylashtirish, dizayn, korsatmalar, ishga tushirish shu jarayonlarda ish olib boriladi."}
            />
            <Card 
                href={"/"}
                img={uch}
                title={"Muammoingizga echimlar"}
                subtitle={"Mavjud brendingizni yaxshilaydigan va mijozlarni oshiradigan texnologiyalar va echimlarni taklif qilamiz."}
            />
        </Styling>
    );
}

const Styling = styled.div`
    display: flex;
    width: 85%;
    margin: 0 5%;
    justify-content: space-between;
`

export default BrendingCards;