import React from 'react';
import { Carousel } from '@trendyol-js/react-carousel';
import styled from 'styled-components';

// images
import carusel1 from '../img/Carusel1/carusel1.png'
import carusel2 from '../img/Carusel1/carusel2.png'
import carusel3 from '../img/Carusel1/carusel3.png'
import carusel4 from '../img/Carusel1/carusel4.png'
import carusel6 from '../img/Carusel1/carusel6.png'
import carusel7 from '../img/Carusel1/carusel7.png'

const Carusel = () => {
    return(
        <Container>
        <Carousel 
                show={3.5} 
                slide={3} 
                swiping={true} 
                rightArrow={null} 
                leftArrow={null}
                infinite={true} 
                transition={0.2} 
                >
           <div>
               <img src={carusel1} alt="carusel1" />
               <h4>Vip fasad</h4>
            </div> 
            <div>
                <img src={carusel2} alt="carusel2" />
                <h4>VeroGroup</h4>
            </div>
            <div>
                <img src={carusel3} alt="carusel3" />
                <h4>Aslbazar</h4>
            </div>
            <div>
                <img src={carusel4} alt="carusel4" />
                <h4>Profilex</h4>
            </div>
            <div>
                <img src={carusel6} alt="carusel6" />
                <h4>Anvar zamon nur</h4>
            </div>
            <div>
                <img src={carusel7} alt="carusel7" />
                <h4>VeroGroup</h4>
            </div>
	    </Carousel>
        </Container>
    )
}

const Container = styled.div`
h4{
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 16px;
    line-height: 22px;
}



`
export default Carusel;