import React from 'react';
import styled from "styled-components"

// component
import Nav from '../page/Nav'

// images

import facebook from '../img/Footer/facebook.png'
import instagram from '../img/Footer/instagram.png'
import telegram from '../img/Footer/telegram.png'

const Footer = () => {
    return(
        <>
        <Styling>
            <div className="container">
            <h1 className="logo">Ultra IT-hub</h1>
            <ul>
                <li>Bosh sahifa</li>
                <li>Biz haqimizda</li>
                <li>Xizmatlar</li>
                <li>Loyihalarimiz</li>
                <li>Mijozlar fikri</li>
                <li>Buyurtma</li>
                <li>Aloqa</li>
            </ul>
            <div className="social">
                <div className="icon">
                    <img src={facebook} alt="facebook" />
                    <img src={instagram} alt="instagram" />
                    <img src={telegram} alt="telegram" />
                </div>
            </div>
            </div>
            
            <p> 2016-2022 OQILA</p>
        </Styling>
        </>
    )
}

const Styling = styled.div`
    position: relative;
    bottom:0;
    background: #F7F8FB;
    display: flex;
    flex-wrap: wrap;
    align-content: flex-start;

    .social{
        display: flex;
    }

    .icon{
        margin: 45px;
    }

    img{
        margin: 0 20px;
    }
    p{
        position: absolute;
        top: 70px;
        margin-left: 4.5rem;
        font-family: Montserrat;
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 17px;
        text-align: center;
    }

    ul {
        display: flex;
        list-style: none;
      }
      li {
        padding-left: 2rem;
        position: relative;
      }
      a {
        font-family: Montserrat;
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 20px;
        text-align: center;
        color: #030303;
        text-decoration: none;
      }
      .logo {
        font-size: 20px;
        font-family: Montserrat;
        font-style: SemiBold
        font-weight: lighter;
        color: #3779FF;
      }

      .container{
        display: flex;
        margin: auto;
        justify-content: space-between;
        align-items: center;
        top: 0;
      }
      margin-top:20px;

`
export default Footer;