import React from 'react';

import styled from 'styled-components';


const SmmCard = ({title, subtitle, img,href}) => {
    return ( 
        <>
        <Styling>
            <div className="top">
                <h1>{title}</h1>
                <img src={img} alt="" />
            </div>
            <hr />
            <p>{subtitle}</p>
            {href && <a href={href}>batafsil ko'rish</a>}
        </Styling>
        </>
     );
}
 const Styling = styled.div`

    margin: 10px;
    width: 550px;
    height: 270px;
    background: #FFFFFF;
    border: 1px solid #A1A1A1;
    box-sizing: border-box;
    .top{
        padding: 1rem 5rem;
        padding-top: 25px;
        display: flex;
        justify-content: space-between;
    }

    h1{
        font-family: Montserrat;
        font-style: normal;
        font-weight: 600;
        font-size: 20px;
        line-height: 26px;
    }

    p{
        padding: 1rem 5rem;
        font-family: Montserrat;
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 22px;
    }

    img{
        width: 30px;
        height: 30px;
    }

    hr{
        border: 1px solid rgba(161, 161, 161, 0.25);
    }
 `
export default SmmCard;