import React from 'react';
import styled from "styled-components"

import Banner from '../components/Banner';
import Carusel from '../components/Carusel';
import MarketingCard from '../components/MarketingCard';
import PortfolioCarusel from '../components/PortfolioCarusel';
import Title from '../components/Title';
import Form from '../components/Form';
import Contact from '../components/Contact';

import banner from '../img/marketing/banner.png'

import Footer from '../components/Footer';
import MarketingSection from '../components/MarketingSection';
import MarketingLink from '../components/MarketingLink';

function MarketingPage() {
    return (
        <div>
            <Banner
            title={"Marketing"}
            buttonBlue={"Loyuhani boshlash"}
            img={banner}
            subtitle={"Ultra IT Hub  butun dunyo boylab orta va yirik kompaniyalar uchun innovatsioninternet-marketing yechimlari smm taklif qiluvchi reklama agentligi."}
            />
            <Title
            title={"Marketing xizmatlari"}
            />
            <MarketingLink/>
            <Title
            title={"Marketing xizmatlarining avzazilgi"}
            />
            <br /><br /><br /><br /><br /><br />
            <MarketingSection/>
            <br /><br /><br /><br /><br /><br />
            <Carusel/>
            <br />
            <Title
                title={"Nima uchun SMM target xizmati \n uchun Ultra IT Hub tanlaysiz?"}
            />
            <br /><br /><br /><br /><br /><br />
            <MarketingCard/>
            <Title
                title={"Mijozlar fikri"}
            />
            <PortfolioCarusel/>
            <Title
                title={"Aloqa"}
            />
            <br /><br /><br /><br /><br />
            <Contact/>
            <Title
                title={"Loyiha buyurtma qiling"}
            />
            <br /><br /><br /><br /><br />
            <Forming>
                <Form/>
            </Forming>
            <Footer/>
        </div>
    );
}

const Forming = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
`

export default MarketingPage;