import React from 'react';
import { Link,Route, Routes } from "react-router-dom";
import styled from 'styled-components';
import Belt from './Belt';



function Menu(props) {
    return (
        <Styling>
            <div className='container'>
                <ul>
                    <li><Link to={"/"}>Veb-dizayn</Link></li>
                    <li><Link to={"/"}>SMM</Link></li>
                    <li><Link to={"/"}>Dasturlash</Link></li>
                    <li><Link to={"/"}>Marketing</Link></li>
                    <li><Link to={"/"}>Brending</Link></li>
                    <li><Link to={"/"}>SEO</Link></li>
                </ul>
            </div>
            <div>
                <Routes>
                    <Route/>
                </Routes>
            </div>
        </Styling>
    );
}

const Styling = styled.div`
    margin-left: 2rem;
    list-style: none;

    ul{
        display:flex;
    }

    li{
        margin-right: 2rem;
        list-style: none;
    }

    a{
        text-decoration: none;
        font-family: Montserrat;
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        color: #A1A1A1;
    }
`

export default Menu;