import React from 'react';
import styled from "styled-components"

import Banner from '../components/Banner';
import Carusel from '../components/Carusel';
import SmmBigCards from '../components/SmmBigCards';
import PortfolioCarusel from '../components/PortfolioCarusel';
import Title from '../components/Title';
import Form from '../components/Form';
import Contact from '../components/Contact';

import design from '../img/banner/design.png'

import Footer from '../components/Footer';
import DizaynCardSection from '../components/DizaynCardSection';

function VebDizayn() {
    return (
        <div>
            <Banner
            title={"Veb-dizayn"}
            buttonBlue={"Loyuhani boshlash"}
            img={design}
            subtitle={"Ultra IT Hub internet marketing studiyasi sizga halol maslahatlar va biznesingizga kerak boladigan eng zor veb-saytlarni tayorlab beradi!"}
            />
            <Title
            title={"Veb saytingiz bolishining avzaliklari"}
            />
            <DizaynCardSection/>
            <Title
            title={"Veb-dizayn loyihalarimiz"}
            />
            <br /><br /><br /><br /><br /><br />
            <Carusel/>
            <br /><br /><br />
            <Title
                title={"Nima uchun web dizayn uchun Ultra IT Hub ni tanlaysiz?"}
            />
            <br /><br /><br />
            <SmmBigCards/>
            <Title
                title={"Mijozlar fikri"}
            />
            <PortfolioCarusel/>
            <Title
                title={"Aloqa"}
            />
            <br /><br /><br /><br /><br />
            <Contact/>
            <Title
                title={"Loyiha buyurtma qiling"}
            />
            <br /><br /><br />
            <Forming>
                <Form/>
            </Forming>
        </div>
    );
}

const Forming = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
`

export default VebDizayn;