import React from 'react';
import styled from "styled-components"

import Card from "./Card"

import bir from '../img/seo/bir.png'
import ikki from '../img/seo/ikki.png'
import uch from '../img/seo/uch.png'

function MarketingCard() {
    return (
        <Styling>
            <Card 
                href={"/"}
                img={bir}
                title={"Mahoratli mutaxasislar"}
                subtitle={"SEO boshqaruv jamoamiz saytingizning ish faoliyatini ko'rib chiqadi va qidiruv tizimlari bilan yanada samarali muloqot qilishingizga yordam beradi"}
            />
            <Card 
                href={"/"}
                img={ikki}
                title={"Togri strategiya"}
                subtitle={"Biz ko'plab ko'rsatkichlarni kuzatib boramiz va minglab ma'lumotlar nuqtalarini tahlil qilamiz, yo'l davomida moslashamiz."}
            />
            <Card 
                href={"/"}
                img={uch}
                title={"Doimiy aloqada bolamiz"}
                subtitle={"Raqobatingiz qanday ketayotgani haqidagi haqiqiy ma'lumotlarni sizga doimiy yetkazib turamiz."}
            />
        </Styling>
    );
}

const Styling = styled.div`
    display: flex;
    width: 85%;
    margin: 0 5%;
    justify-content: space-between;
`

export default MarketingCard;