import React, { useState } from 'react';
import Axios from 'axios'
import styled from "styled-components"

const Form = () => {
    const uri = ""
    const [data, setData] = useState({
        name: "",
        telefon: "",
        email: "",
        project: "",
    })

    function submit(e){
        e.preventDefault();
        alert("submitted")
        Axios.post(uri,{
            name: data.name,
            telefon: data.telefon,
            email: data.email,
            project: data.project,
            description: data.description,
        })
        .then(res=>{
            console.log(res.data)
        })
    }

    function handle(e){
        const newdata={...data}
        newdata[e.target.id] = e.target.value
        setData(newdata)
        console.log(newdata)
    }
    
    return ( 
        <StyledForm>
            <form onSubmit={(e)=> submit(e)}>
                <div className='container'>
                    <div className='formStyle'>
                        <label htmlFor="userName">Ism</label>
                        <input onChange={(e) => handle(e)} value={data.name}  id="name"  type="text" />
                    </div>
                    <div className='formStyle'>
                        <label htmlFor="phoneNumber">Telefon</label>
                        <input onChange={(e) => handle(e)} value={data.telefon} id="telefon"   type="text" />
                    </div>
                </div>
                <div className='container'>
                    <div className='formStyle'>
                        <label htmlFor="email">Email</label>
                        <input onChange={(e) => handle(e)} value={data.email}  id="email"   type="email" />
                    </div>
                    <div className='formStyle'>
                        <label htmlFor="prrojrctName">Loyiha nomi</label>
                        <input onChange={(e) => handle(e)} value={data.project} id="project"  type="text" />
                    </div>
                </div>
                <div className='area' >
                    <label className='textBox' htmlFor="description">Loyiha haqida qizqacha</label>
                    <textarea onChange={(e) => handle(e)} value={data.description} id="description" type="text" ></textarea>
                    <button >Yuborish</button>
                </div>
            </form>
        </StyledForm>
     );
}

const StyledForm = styled.div`



label{
    display: block;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
}
input{
    display: block;
    width: 295px;
    height: 40px;
    background: #F7F8FB;
    border: 1px solid #D0D0D0;
    box-sizing: border-box;
}
textarea{
    display: block;
    width: 620px;
    height: 160px;
    background: #F7F8FB;
    border: 1px solid #D0D0D0;
}
.container{
    display: flex;
    padding: 20px;
}

button{
    width: 132px;
    height: 50px;
    background: #3779FF;
    color: #fff;
    border: none;
    margin-top: 40px;
}

.formStyle{
    padding: 20px;
    text-align: initial;
}
.area{
    text-align: initial;
    margin-left: 40px;
}


`
 
export default Form;