import React from 'react';
import styled from "styled-components"

import Card from "./Card"

import book from '../img/smm/book.png'
import target from '../img/smm/target.png'
import key from '../img/smm/key.png'

function SmmBigCards() {
    return (
        <Styling>
            <Card 
                href={"/"}
                img={target}
                title={"Mahoratli targetolog"}
                subtitle={"Sizningmahsulotingiz ijtimoiy tarmoqlarda mashxur bolishi uchun mahoratli va oz ishini eng zor targetologi."}
            />
            <Card 
                href={"/"}
                img={book}
                title={"Toliq marketing strategiyasi"}
                subtitle={"Biz shunchaki reklama hisoblaringizni boshqarmaymiz, balki eng katta muammolaringizni hal qilish va maqsadlaringizga erishishingizga yordam beradigan eng yaxshi platformalar va xizmatlardan foydalangan holda toliq marketing strategiyasini taqdim etamiz."}
            />
            <Card 
                href={"/"}
                img={key}
                title={"Muammoingizga echimlar"}
                subtitle={"7000 dan ortiq Google va Facebook reklama tekshiruvlari orqali biz sizni yaxshi korinishga keltirish va muvaffaqiyatga erishish uchun nimalar kerakligini bilib olamiz va yechim beramiz"}
            />
        </Styling>
    );
}

const Styling = styled.div`
    display: flex;
    width: 90%;
    margin: 0 5%;
    justify-content: space-between;
`

export default SmmBigCards;