import React from 'react';
import styled from "styled-components"

import Banner from '../components/Banner';
import Carusel from '../components/Carusel';
import BrendingCards from '../components/BrendingCards';
import PortfolioCarusel from '../components/PortfolioCarusel';
import Title from '../components/Title';
import Form from '../components/Form';
import Contact from '../components/Contact';

import banner from '../img/brending/banner.png'

import Footer from '../components/Footer';
import BrendingSection from '../components/BrendingSection';

function BrendingPage() {
    return (
        <div>
            <Banner
            title={"Brending"}
            buttonBlue={"Loyuhani boshlash"}
            img={banner}
            subtitle={"Strategiyaga asoslangan, dizaynga asoslangan va raqam uchun qurilgan bizning ishimiz dunyodagi eng sevimli brendlarning madaniy va ijodiy qiymatini ochib beradi."}
            />
            <Title
            title={"Brending xizmatini ijobiy tomonlari"}
            />
            <BrendingSection/>
            <Title
            title={"Loyihalarimizi"}
            />
            <br /><br /><br /><br /><br /><br />
            <Carusel/>
            <br />
            <Title
                title={"Nima uchun brending xizmati uchun Ultra IT Hub tanlaysiz?"}
            />
            <br /><br /><br /><br /><br /><br />
            <BrendingCards/>
            <Title
                title={"Mijozlar fikri"}
            />
            <PortfolioCarusel/>
            <Title
                title={"Aloqa"}
            />
            <br /><br /><br /><br /><br />
            <Contact/>
            <Title
                title={"Loyiha buyurtma qiling"}
            />
            <br /><br /><br /><br /><br />
            <Forming>
                <Form/>
            </Forming>
            <Footer/>
        </div>
    );
}

const Forming = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
`

export default BrendingPage;