import React from 'react';
import styled from 'styled-components';

function CardAnother({secontTitle,text, hreff}) {
    return (
            <CardSS href={hreff}>

                { secontTitle && <h1>{secontTitle}</h1>}
                { text && <p>{text}</p>}

            </CardSS> 
    );
}

const CardSS = styled.a`
    padding: 50px;
`

export default CardAnother;