import React from 'react';
import styled from "styled-components"

import Banner from '../components/Banner';
import Carusel from '../components/Carusel';
import SmmBigCards from '../components/SmmBigCards';
import PortfolioCarusel from '../components/PortfolioCarusel';
import Title from '../components/Title';
import Form from '../components/Form';
import Contact from '../components/Contact';

import smm from '../img/banner/smm.png'

import Footer from '../components/Footer';
import SmmCardSection from '../components/SmmSection';

function SMMPage() {
    return (
        <div>
            <Banner
            title={"Smm"}
            buttonBlue={"Loyuhani boshlash"}
            img={smm}
            subtitle={"Ultra IT Hub  butun dunyo boylab orta va yirik kompaniyalar uchun innovatsioninternet-marketing yechimlari smm taklif qiluvchi reklama agentligi."}
            />
            <Title
            title={"SMM ijobiy tomonlari"}
            />
            <SmmCardSection/>
            <Title
            title={"SMM loyihalarimizi"}
            />
            <br /><br /><br /><br /><br /><br />
            <Carusel/>
            <br />
            <Title
                title={"Nima uchun SMM target xizmati \n uchun Ultra IT Hub tanlaysiz?"}
            />
            <br /><br /><br /><br /><br /><br />
            <SmmBigCards/>
            <Title
                title={"Mijozlar fikri"}
            />
            <PortfolioCarusel/>
            <Title
                title={"Aloqa"}
            />
            <br /><br /><br /><br /><br />
            <Contact/>
            <Title
                title={"Loyiha buyurtma qiling"}
            />
            <br /><br /><br /><br /><br />
            <Forming>
                <Form/>
            </Forming>
        </div>
    );
}

const Forming = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
`

export default SMMPage;