import React from 'react';
import styled from "styled-components"
import {Link} from "react-router-dom";

import ContactCard from "../components/ContactCard"

import location from '../img/contact/location.png'
import phone from '../img/contact/phone.png'
import mail from '../img/contact/mail.png'
import time from '../img/contact/time.png'

// images

import facebook from '../img/contact/facebook.png'
import instagram from '../img/contact/instagram.png'
import telegram from '../img/contact/telegram.png'
import Title from '../components/Title';

function Contact() {
    return (
        <>
        <Styling>
            <Title
            title={"Biz bilan bog'laning"}
            />
            <div className='Cards'>
                <ContactCard  
                    img={location}
                    title={"Manzil"}
                    subtitle={"Mirzo Ulug'bek ko'ch., 64-uy, Toshkent sh., O'zbekiston"}
                />
                <ContactCard 
                    img={phone}
                    title={"Telefon"}
                    subtitle={"(71) 268 43-60"}
                />
                <ContactCard 
                    img={mail}
                    title={"E-mail"}
                    subtitle={"info@oqila.uz"}
                />
                <ContactCard 
                    img={time}
                    title={"Aloqa vaqti"}
                    subtitle={"Dushanba - Juma"}
                />
            </div>
            <div className="social">
                <div className="icon">
                <Link to="Url"> <img src={facebook} alt="facebook" /></Link>
                <Link to="Url"> <img src={instagram} alt="facebook" /></Link>
                <Link to="Url"> <img src={telegram} alt="facebook" /></Link>
                </div>
            </div>

        </Styling>

        
        </>
            );
}

const Styling = styled.div`
    width:90%;
    min-height:650px;
    display: flex;
    flex-direction:column;
    justify-content:space-between;
    width: 90%;
    margin: 20px auto;
    margin-bottom:60px;

    .Cards{
        width:100%;
        display:flex;
        justify-content:space-between;
    }
    .social{
        width:100%;
        display:flex;
        justify-content:space-around;
    }
    .icon{
        width:80%;
        display:flex;
        justify-content:space-around;
    }
    .icon img{
        width:120px;
    }
    .icon img{
        
    }
`

export default Contact;