import React from 'react';
import SmmCard from './SmmCard';
import styled from "styled-components"

import one from "../img/brending/one.png"
import two from "../img/brending/two.png"
import three from "../img/brending/three.png"
import four from "../img/brending/four.png"
import five from "../img/brending/five.png"

const BrendingSection = () => {
    return ( 
        <Container>
            <SmmCard
            title={"Brendlash sodiqlikka erishadi"}
            subtitle={"Koproq daromat qiish uchun yangi mijozlarni jalb qilishga ko'proq pul sarflashdan ko'ra, sodiq mijozlaringizga ko'proq e'tibor qaratishingiz mumkin va bu daromatingizni oshiradi."}
            img={one}
            />
            <SmmCard
            title={"Sizni alohida ajratib turadi"}
            subtitle={"Har bir kompaniya o'z mijozlarining e'tiborini jalb qilishga harakat qilmoqda va odamlar bir kunda yuzlab reklamalar bilan bombardimon qilinmoqda, ularni ichida sizni ajratib olishi oson."}
            img={two}
            />
            <SmmCard
            title={"To'g'ri brend bozorda ishonchni yaratadi"}
            subtitle={"Brendingiz  halollikka asoslangan bo'lsa, odamlar sizning brendingizga ishonishni boshlaydilar. Bu ko'proq mijozlarni jalb qiladi, balki butun bozorda ishonchni oshiradi."}
            img={three}
            />
            <SmmCard
            title={"Moliyaviy inqirozdan qutqarishi mumkin"}
            subtitle={"Brendlash qiyin kunlarda marketing uchun ko'p mablag 'sarflamasalar ham, bu ularni muvaffaqiyat yo'lida ushlab turuvchi vosita."}
            img={four}
            />
            <SmmCard
            title={"Brend sizning xodimlaringizni ilhomlantiradi"}
            subtitle={"Ajoyib brend sizning hodimlaringizni ilhomlantiradi, ayniqsa, katta biznesni qurish haqida gap ketganda, siz ko'p narsaga sodiq va boshqaruvchilikka muhtoj bo'lgan odamlarga muhtoj bo'lasiz."}
            img={five}
            />
        </Container>
     );
}

const Container = styled.div`
width: 90%;
margin: 5% auto;
display: flex;
flex-wrap: wrap;
`
 
export default BrendingSection;