import React from 'react';
import styled from "styled-components"

import Banner from '../components/Banner';
import Carusel from '../components/Carusel';
import SeoCards from '../components/SeoCards';
import PortfolioCarusel from '../components/PortfolioCarusel';
import Title from '../components/Title';
import Form from '../components/Form';
import Contact from '../components/Contact';

import seo from '../img/brending/seo.png'

import Footer from '../components/Footer';
import SeoSection from '../components/SeoSection';

function SEOPage() {
    return (
        <div>
            <Banner
            title={"SEO"}
            buttonBlue={"Loyuhani boshlash"}
            img={seo}
            subtitle={"Ultra IT Hub internet marketing kompaniyasi SEO tahlil xizmati yonalishda yetakchilardan bolib, uning tajribali mutaxassislari kozlagan maqsadingizga erishishingizga yordam beradi."}
            />
            <Title
            title={"SEO xizmatini ijobiy tomonlari"}
            />
            <SeoSection/>
            <Title
            title={"Loyihalarimizi"}
            />
            <br /><br /><br /><br /><br /><br />
            <Carusel/>
            <br />
            <Title
                title={"Nima uchun SEO xizmati uchun Ultra IT Hub tanlaysiz?"}
            />
            <br /><br /><br /><br /><br /><br />
            <SeoCards/>
            <Title
                title={"Mijozlar fikri"}
            />
            <PortfolioCarusel/>
            <Title
                title={"Aloqa"}
            />
            <br /><br /><br /><br /><br />
            <Contact/>
            <Title
                title={"Loyiha buyurtma qiling"}
            />
            <br /><br /><br /><br /><br />
            <Forming>
                <Form/>
            </Forming>
            <Footer/>
        </div>
    );
}

const Forming = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;
`

export default SEOPage;