import React from 'react';
import styled from 'styled-components'
import {Link} from "react-router-dom";

const Card = ({title,subtitle,img,href,}) => {
    return(
        <>
            { href &&  <CardStyle href={href}>
                {img && <img src={img} alt="web" />}
                {title && <h2 className="cardTitle">{title}</h2>}
                {subtitle && <p className="textCard">{subtitle}</p>}
            </CardStyle>}
        </>
    )
}

const CardStyle = styled.a`{
    width: 378px;
    min-height:238px;
    text-decoration: none;
    color: #000;
    background: #FFFFFF;
    border: 1px solid #A1A1A1;
    box-sizing: border-box;
    padding: 44px 40px 40px 40px;
    margin:20px;
}

.textCard{
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 22px;
}

.cardTitle{
    font-family: Montserrat;
    font-style: normal;
    font-weight: 600;
    font-size: 20px;
    line-height: 26px;
}

`





export default Card;