import React from 'react';
import styled from 'styled-components';


import BeltCard from './BeltCard';

import People from '../img/Belt/People.png'
import Medal from '../img/Belt/Medal.png'
import Headphone from '../img/Belt/Headphone.png'

function Belt(props) {
    return (
        <Styling>
            <div className="container">
                <BeltCard
                    img={People}
                    title={"Sizning loyihangiz sifatiga javobgar xodimlar jamoasi"}
                />
                <BeltCard
                    img={Medal}
                    title={"Sizning loyihangiz sifatiga javobgar xodimlar jamoasi"}
                />
                <BeltCard
                    img={Headphone}
                    title={"Sizning loyihangiz sifatiga javobgar xodimlar jamoasi"}
                />
            </div>
        </Styling>
    );
}

const Styling = styled.div`
    width: 100%;
    margin: 7rem auto;
    background: #F7F8FB;
    .container{
        display: flex;
        width: 90%;
        margin 0 auto;
        text-align: initial;
    }
`

export default Belt;