import React from 'react';
import SmmCard from './SmmCard';
import styled from "styled-components"

import people from "../img/smm/people.png"
import star from "../img/smm/star.png"
import dollor from "../img/smm/dollor.png"
import heart from "../img/smm/heart.png"

const SmmCardSection = () => {
    return ( 
        <Container>
            <SmmCard
            title={"Keng qamrovli bozor"}
            subtitle={"Yer yuzida 3,78 milliard odam ijtimoiy tarmoqdan foydalanadi, va isstemolchilar sizning biznesingizdan internet orqali xabar topadi,  bu oz-ozidan mijozlar bazasini kenaytiradi."}
            img={people}
            />
            <SmmCard
            title={"Keng qamrovli bozor"}
            subtitle={"77,6% kompaniyalar SMM xizmati o'rqali o'z biznesini targ'ib qilyapti."}
            img={star}
            />
            <SmmCard
            title={"Keng qamrovli bozor"}
            subtitle={"73% marketologlar SMM xizmatini o'z bizneslari uchun juda samarali deb hisoblashdi"}
            img={dollor}
            />
            <SmmCard
            title={"Keng qamrovli bozor"}
            subtitle={"Iste'molchilarning 49% ijtimoiy tarmoqdagi SMM targeting orqali brendga ishonchi oshdi, chunki songi ikki yilda barcha biznes egalari oz mahsulotlarini SMM target bilan bogladi."}
            img={heart}
            />
        </Container>
     );
}

const Container = styled.div`
width: 90%;
margin: 5% auto;
display: flex;
justify-content:space-between;
flex-wrap: wrap;
`
 
export default SmmCardSection;