import React from 'react';
import SmmCard from './SmmCard';
import styled from "styled-components"

import one from "../img/dizayn/one.png"
import two from "../img/dizayn/two.png"
import three from "../img/dizayn/three.png"
import four from "../img/dizayn/four.png"

const DizaynCardSection = () => {
    return ( 
        <Container>
            <SmmCard
            title={"Xarajatlarni tejash"}
            subtitle={"Sizning biznes maqsadlaringizga erishish uchun maxsus ishlab chiqilgan raqobatbardosh SaaS vositalari va talab bo'yicha xizmat ko'rsatish platformalari bilan xarajatlaringizni kamaytiring."}
            img={one}
            />
            <SmmCard
            title={"Mijozlar bazasini kopaytirish"}
            subtitle={"Mijozlaringizni sizga qayta-qayta murojaat qilishga undaydigan maxsus ishlab chiqilgan CRM, Saas va talab bo'yicha tizimlar bilan kengaytiring."}
            img={two}
            />
            <SmmCard
            title={"Yaxshilash va avtomatlashtirish"}
            subtitle={"Boshqa analitik vositalar va veb-yechimlar bilan bir qatorda buyurtma qilingan CRM/ERP tizimlari bilan vaqtingiz va resurslaringizdan samarali foydalaning."}
            img={three}
            />
            <SmmCard
            title={"Onlayn mavjjudligi"}
            subtitle={"Onlayn biznes imkoniyatlaridan foydalaning va saytni ishlab chiqish xizmatlarimiz bilan ko'proq mijozlarga ko'rinadigan bo'ling."}
            img={four}
            />
        </Container>
     );
}

const Container = styled.div`
width: 90%;
margin: 5% auto;
display: flex;
justify-content:space-between;
flex-wrap: wrap;
`
 
export default DizaynCardSection;