import React from 'react';
import styled from "styled-components"

import Card from "./Card"

import avatar from '../img/programming/avatar.png'
import lock from '../img/programming/lock.png'
import book from '../img/programming/book.png'

function SmmBigCards() {
    return (
        <Styling>
            <Card 
                href={"/"}
                img={avatar}
                title={"Mahoratli dasturchilar"}
                subtitle={"Sizning veb saytingiz mukammal ishlashligi uchun mahoratli va oz ishini eng zor dasturchilar jamoasi"}
            />
            <Card 
                href={"/"}
                img={lock}
                title={"Xavfsiz veb sayt"}
                subtitle={"Biz butun veb-ishlab chiqish jarayonimizga test o'tkazamiz va veb-ishlab chiquvchilarimiz yuqori xavfsizlik standartlariga rioya qilishadi."}
            />
            <Card 
                href={"/"}
                img={book}
                title={"Muammoingizga echimlar"}
                subtitle={"Veb-ilovangizni yaxshilaydigan va ishlab chiqish xarajatlarini kamaytiradigan texnologiyalar va echimlarni taklif qilamiz."}
            />
        </Styling>
    );
}

const Styling = styled.div`
    display: flex;
    width: 90%;
    margin: 0 5%;
    justify-content: space-between;
    border:1px solid red;
`

export default SmmBigCards;