import { Component } from "react";
import Carousel from "react-multi-carousel";
import "react-multi-carousel/lib/styles.css";
import styled from 'styled-components';


import carusel1 from '../img/Carusel1/carusel1.png'
import carusel2 from '../img/Carusel1/carusel2.png'
import carusel3 from '../img/Carusel1/carusel3.png'
import carusel4 from '../img/Carusel1/carusel4.png'
import carusel6 from '../img/Carusel1/carusel6.png'
import carusel7 from '../img/Carusel1/carusel7.png'



const responsive = {
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 3,
      slidesToSlide: 3 // optional, default to 1.
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 2,
      slidesToSlide: 2 // optional, default to 1.
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1,
      slidesToSlide: 1 // optional, default to 1.
    }
  };


class DemoCarousel extends Component {
    render() {
        return (
            <Styled>
                <Carousel
                    swipeable={true}
                    draggable={false}
                    showDots={true}
                    responsive={responsive}
                    ssr={true} // means to render carousel on server-side.
                    infinite={true}
                    autoPlay={this.props.deviceType !== "mobile" ? true : false}
                    autoPlaySpeed={10000}
                    keyBoardControl={true}
                    customTransition="all .5"
                    transitionDuration={500}
                    containerClass="carousel-container"
                    removeArrowOnDeviceType={["tablet", "mobile"]}
                    deviceType={this.props.deviceType}
                    dotListClass="custom-dot-list-style"
                    itemClass="carousel-item-padding-40-px"
                    >
                    <div><img src={carusel1} alt="" /></div>
                    <div><img src={carusel2} alt="" /></div>
                    <div><img src={carusel3} alt="" /></div>
                    <div><img src={carusel4} alt="" /></div>
                </Carousel>
            </Styled>
        );
    }
};


const Styled = styled.div`
height: 5rem;
// .carousel-container
// {
//     position:absolute;
// }
..react-multiple-carousel__arrow
{
    position:absolute;
    margin-left:-120px;
}
    img{
        height: 10rem;
        width: 10rem;
    }
`


export default DemoCarousel;