import React from 'react';
import styled from 'styled-components';

import Veb from "../img/CardsMainPage/Veb.png"
import Smm from "../img/CardsMainPage/Smm.png"
import Programming from "../img/CardsMainPage/Programming.png"
import Marketing from "../img/CardsMainPage/Marketing.png"
import Brending from "../img/CardsMainPage/Brending.png"
import Seo from "../img/CardsMainPage/Seo.png"
import Card from './Card';



const Cards = () => {
    return(
        <CardsStyle >
            <Card
                title={"Veb-dizayn"}
                subtitle={"Innovatsion, kreativ yondashilgan, kompaniya brendini mohiyatini to'liq aks ettiruvchi dizayn yaratamiz"}
                img={Veb}
                href="/page/webdizayn"
            />
            <Card
                title={"SMM"}
                subtitle={"Ijtimoiy media marketingi bo'yicha trafikni boshqarish, tashrif buyuruvchilarni kopaytirish"}
                img={Smm}
                href="/page/smm"
            />
            <Card
                title={"Dasturlash"}
                subtitle={"Saytingiz mijozlarni jalb qiladigan, daromatni olib keladigan va mukammal ishlaydigan sayt boladi"}
                img={Programming}
                href="/page/programming"
            />
            <Card
                title={"Marketing"}
                subtitle={"Mahsulotingiz bozorda oz orniga ega bolishi va savdo xajmini oshirish uchun marketing xizmati"}
                img={Marketing}
                href="/page/marketing"
            />
            <Card
                title={"Brending"}
                subtitle={"Bizning kreativ jamoa bilan brendingiz mijozlarni jalb qiluvchi va noyob brending xizmatlariga ega boladi"}
                img={Brending}
                href="/page/brending"
            />
            <Card
                title={"SEO"}
                subtitle={"Googleda tabiy yuqoriga ko'tarilish va mijozlar o'z - o'zidan ko'payaverishi uchun SEO xizmati mavjud"}
                img={Seo}
                href="/page/seo"
            />
            
        </CardsStyle>
    )
}

const CardsStyle = styled.div`

width: 90%;
margin: 0 auto;

display: flex;
flex-wrap:wrap;
justify-content:space-around;

// grid-template-columns: repeat(3, 1fr);
// grid-row-gap: 20px;
// grid-column-gap: 20px;

`
export default Cards;