import React, { useId } from 'react';
import styled from 'styled-components';
import {Link} from "react-router-dom";


import {Routes,Route} from "react-router-dom";


import MainPage from './MainPage'
import AboutUsPage from './AboutUsPage'
import MarketingPage from './MarketingPage'
import OrderingPage from './OrderingPage'
import ProgrammingPage from './ProgrammingPage'
import SEOPage from './SEOPage'
import SMMPage from './SMMPage'
import VebDizayn from './VebDizayn'
import BrendingPage from './BrendingPage'
import Cards from '../components/Cards';
import Portfolio from '../components/PortfolioCarusel';

import { NavDropdown,Nav, NavItem } from 'react-bootstrap';
import 'bootstrap/dist/css/bootstrap.min.css';
import Contact from '../page/Contact';



function NavBar() {


    return (

        <>
        
        <StyledNav>
            <h1 className="logo">Ultra IT-HUB</h1>

            <ul>
                <li><Link to="/">Bosh sahifa</Link></li>
                <li><Link to="/page/aboutus">Biz haqimizda</Link></li>
                <li>
                <NavDropdown title="Xizmatlarimiz" >
                  <NavDropdown.Item ><Link to="/page/aboutus">Veb Dizayn</Link></NavDropdown.Item>
                  <NavDropdown.Item ><Link to="/page/brending">SMM</Link></NavDropdown.Item>
                  <NavDropdown.Item ><Link to="/page/programming">Dasturlash</Link></NavDropdown.Item>
                  <NavDropdown.Item ><Link to="/page/marketing">Marketing</Link></NavDropdown.Item>
                  <NavDropdown.Item ><Link to="/page/brending">Brending</Link></NavDropdown.Item>
                  <NavDropdown.Item ><Link to="/page/seo">SEO xizmati</Link></NavDropdown.Item>
                </NavDropdown>
                </li>
                <li><Link to='/page/aboutus'>Loyihalarimiz</Link></li>
                <li><Link to='/portfolio'>Mijozlar fikri</Link></li>
                <li><Link to='/order'>Buyurtma</Link></li>
                <li><Link to='/contact'>Aloqa</Link></li>
            </ul>
            
        </StyledNav>

        <div>
            <Routes>
                <Route path="/" exact element={<MainPage/>} />
                <Route path="/page/aboutus" element={<AboutUsPage/>} />
                <Route path="/page/brending" element={<BrendingPage/>} />
                <Route path="/page/marketing" element={<MarketingPage/>} />
                <Route path="/page/ordering" element={<OrderingPage/>} />
                <Route path="/page/programming" element={<ProgrammingPage/>} />
                <Route path="/contact" element={<Contact/>} />
                <Route path="/page/seo" element={<SEOPage/>} />
                <Route path="/page/smm" element={<SMMPage/>} />
                <Route path="/page/webdizayn" element={<VebDizayn/>} />
                <Route path="/services" element={<Cards/>} />
                <Route path="/portfolio" element={<Portfolio/> } />
                <Route path="/order" element={<OrderingPage/> } />
            </Routes>
        </div>

        </>

    );
}

const StyledNav = styled.nav`
  width:100%;
  height: 10vh;
  display: flex;
  margin: auto;
  justify-content: space-between;
  align-items: center;
  padding: 0rem 5rem;
  position: sticky;
  background-color:#E5E7E9 ;
  top: 0;
  z-index: 10;
  
  ul {
    display: flex;
    list-style: none;
    flex-wrap:wrap;
  }

  .btn-basic
  {
    border:none;
  }
  .btn-basic:focus
  {
    outline:none;
  }
  li {
    height:45px;
    padding-left: 2rem;
    position: relative;
    display:flex;
    justify-content:center;
    align-items:center;
  }
  a {
    font-family: Montserrat;
    font-style: normal;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    text-align: center;
    color: #030303;
    text-decoration: none;
  }
  .logo {
    font-size: 20px;
    font-family: Montserrat;
    font-style: SemiBold
    font-weight: lighter;
    color: #3779FF;
  }

  .dropdown-menu
  {
    border-radius:0;
  }  
  .dropdown-menu a:hover
  {
    color:#r77
  }
  
`

export default NavBar;