import React from 'react';
import SmmCard from './SmmCard';
import styled from "styled-components"

import one from "../img/marketing/one.png"
import two from "../img/marketing/two.png"
import three from "../img/marketing/three.png"
import four from "../img/marketing/four.png"

const MarketingLink = () => {
    return ( 
        <Container>
            <SmmCard
            href={"/"}
            title={"Mijozlar kopayishi"}
            subtitle={"SEO-ni amalga oshirish orqali siz saytingiz oladigan malakali yetakchilar miqdorini oshirishingiz mumkin. Bu mijoz bo'lish imkoniyati yuqori bo'lgan qidiruvchilarni jalb qilishni anglatadi."}
            img={one}
            />
            <SmmCard
            href={"/"}
            title={"Sizni alohida ajratib turadi"}
            subtitle={"Agar sizning biznesingiz jismoniy joylarga ega bo'lsa va sizning maqsadingiz iste'molchilarning ularga kelishini ta'minlash bo'lsa, geo-targeting juda foydali bo'lishi mumkin."}
            img={two}
            />
        </Container>
     );
}

const Container = styled.div`
width: 90%;
margin: 5% auto;
display: flex;
flex-wrap: wrap;
`
 
export default MarketingLink;