import React from 'react';
import Banner from "../components/Banner"
import BeltTwo from '../components/BeltTwo';
import Belt from '../components/Belt';
import Title from '../components/Title';
import LogoBelt from '../components/LogoBelt';
import Form from '../components/Form';

import styled from "styled-components"

import aboutus from "../img/aboutus/aboutus.png"
import third from "../img/banner/third.png"
import PortfolioCarusel from '../components/PortfolioCarusel';
import Contact from '../components/Contact';
import Footer from '../components/Footer';

const AboutUsPage = () => {
    return ( 
        <>
        <Style>
            <Banner
                title={"Biz bilan yaqindan tanishing"}
                subtitle={"Bizda faqat daromad olib keladigan internet marketing xizmatlari"}
                img={aboutus}
            />
            <Title
                title={"Gayratli jamoa"}
            />
            <p className='groupInfo'>Jamoamiz gayratli va istedodli dizayner, marketolog va dasturchilar, bosh hisobchi va menejerlardan tashkil topgan. 2016 yildan buyon birga ishlab kelamiz. Erishgan yutuqlarimizdan fahrlanamiz. Mutaxassislarimiz jahonning obroyli oliygohlari bitiruvchilari hisoblanishadi.</p>
            <BeltTwo/>
            <img src={third} />
            <br /><br />
            <Title
                title={"Chuqur bilimga asoslangan xizmatlar"}
                />
                <p className='groupInfo'>Texnologik xizmatlarni korsatish katta bilim talab qiladi. Kerakli natijaga yetib borish uchun katta tajriba, hisob-kitob va sabr kerak boladi. Chunchaki chotdan baraka ishlab bolmaydi. Bizning jamoa esa osha bilimlarga ega va oz mijozlariga natija asoslanganxizmatlarni korsatib kelmoqda.</p>
            <Belt/>
            <Title
                title={"Mijozlar fikri"}
            />
            </Style>
            <PortfolioCarusel/>
            <LogoBelt/>
            <Title 
            title={"Aloqa"}
            subtitle={"biz bilan boglanish imkoni"}
            />
            <Contact/>
            <Title
                title={"Loyiha buyurtma qiling"}
            />
            
            
            <Forming>
                <Form/>
            </Forming>
        </>
     );
}

const Forming = styled.div`
    display: flex;
    align-items: center;
    justify-content: center;



`
const Style = styled.div`
display:flex;
justify-content:center;
align-items:center;
flex-direction:column;
.groupInfo{
    display:block;
    width:90%;
    margin:45px 5%;
    text-align:left;
    
}

`
 
export default AboutUsPage;