import React from 'react';
import styled from 'styled-components';

const Title = ({ title, subtitle, id}) => {
    return(
        <div>
        <MyTitle>
            {title && <h1 className='title'>{title}</h1>}
            {subtitle && <h5 className='titleTwo'>{subtitle}</h5>}
        </MyTitle>
        </div>
    )
}

const MyTitle = styled.div`
    text-align: initial;
    width: 50%;
    margin 0 5%;
    .title::before {
        content: "";
        display: inline-block;
        margin-bottom: 10px;
        width: 28px;
        height: 2px;
        background: #3779FF;
    }
    .title{
        min-width:550px;
        position: absolute;
        display:block;
        float:left;
        left: 75px;
        height: 29px;
        font-size: 24;
        line-height: 39px;
        font-style: normal;
        font-weight: 600;
        font-family: 'Montserrat';
    }
    .titleTwo{
        min-width:500px;
        width:70%;
        padding-top:50px ;
        font-family: 'Montserrat';
        font-style: normal;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        text-transform:uppercase;
        color: #3777FF;
    }
    @media screen and (max-width: 1200px) {
        width:1000px;
    }
`

export default Title;